import React, { Component } from 'react';
import { Layout, Menu, Button, Drawer, Divider, Collapse, Popover, Spin } from 'antd';
import Link from 'next/link';
import { withRouter } from 'next/router';
import Fade from 'react-reveal/Fade';
import { connect } from 'react-redux';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mobileMenu: false,
      fixed: false,
      addClass: false,
      selected: undefined,
    }
    // this.widgetUrl = window.location.origin.indexOf('sandbox') !== -1 || window.location.origin.indexOf('localhost') !== -1 ? "https://widget-sandbox.botgate.ai/botgate.js?v=2" : "https://widget.botgate.ai/botgate.js?v=2";
  }


  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
  }

  getDaysLeft = (date) => {
    const date1 = new Date(date);
    const date2 = new Date(Date.now());
    const diffTime = Math.abs(date2.getTime() - date1.getTime());
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    return diffDays;
  }

  onScroll = (e) => {
    let top;
    top = (document.documentElement ? document.documentElement.scrollTop : null) || (document.scrollingElement ? document.scrollingElement.scrollTop : null) || 0;
    if (this.props.router.pathname === "/") {
      if (top > 32) {
        document.getElementById("navbar").classList.contains('fixed') ? null : document.getElementById("navbar").classList.add('fixed')
      }
      else {
        document.getElementById("navbar").classList.contains('fixed') ? document.getElementById("navbar").classList.remove('fixed') : null
      }
    }
  };

  render() {
    let { fixed, addClass, selected } = this.state;
    let { userDetails, subscription, loggedIn, loading, logout } = this.props

    // subscription && subscription.plan ? subscription.plan.isTrial = true : null;
    // let oauthUrl = window.location.origin.indexOf('sandbox') !== -1 || window.location.origin.indexOf('localhost') !== -1 ? "https://oauth-sandbox.botgate.ai" : "https://oauth.botgate.ai"
    return (
      <React.Fragment>
        {/* <div className="banner mobile-hide"> Whatsapp is on preview now! Pre-built bots will be ready to test. Stay tuned. </div> */}

        <Layout.Header className={`${fixed || this.props.router.pathname !== "/" ? 'fixed' : ''}`} ref={el => this.navbar = el} id="navbar" >
          <div className="container">
            <Link key="/" href="/">
              <a onClick={() => this.setState({ selected: [''] })}>
                <img src="/static/img/logo/logo-dark.svg" />
              </a>
            </Link>
            <div className="menu-right">
              <Menu
                theme="light"
                mode="horizontal"
                defaultSelectedKeys={[this.props.router.pathname.substring(1, this.props.router.pathname.length)] || ['']}
                selectedKeys={selected || undefined}
              >
                <Menu.Item className="font-bilgetech" key="features"><Link href="/features"><a onClick={() => this.setState({ selected: ['features'] })}>Features </a></Link></Menu.Item>
                <Menu.Item className="font-bilgetech" key="integrations"><Link href="/integrations"><a onClick={() => this.setState({ selected: ['integrations'] })}>Integrations </a></Link></Menu.Item>

                <Menu.SubMenu title={<span className="submenu-title-wrapper font-bilgetech">Solutions </span>}>
                  <Menu.Item key="e-commerce"><Link href="/solutions/e-commerce"><a onClick={() => this.setState({ selected: ['e-commerce'] })}>E-Commerce</a></Link></Menu.Item>
                  <Menu.Item key="lead-generation"><Link href="/solutions/lead-generation"><a onClick={() => this.setState({ selected: ['lead-generation'] })}>Lead Generation</a></Link></Menu.Item>
                  <Menu.Item key="customer-support"><Link href="/solutions/customer-support"><a onClick={() => this.setState({ selected: ['customer-support'] })}>Customer Support</a></Link></Menu.Item>
                  <Menu.Item key="hr-process"><Link href="/solutions/hr-process"><a onClick={() => this.setState({ selected: ['hr-process'] })}>HR Process</a></Link></Menu.Item>
                </Menu.SubMenu>


                <Menu.Item className="font-bilgetech" key="pricing"><Link href="/pricing"><a onClick={() => this.setState({ selected: ['pricing'] })}>Pricing </a></Link></Menu.Item>
                {/* <Menu.Item className="font-bilgetech" key="blog"><Link href="/blog"><a>Blog</a></Link></Menu.Item> */}
              </Menu>
              <div className={`header-login ${loggedIn === null ? 'loading' : loggedIn ? subscription && subscription.plan && subscription.plan.isTrial ? 'trial' : 'enterprise' : 'guest'}`}>
                {
                  loading ?  // DESKTOP
                    <Spin className="header-not-logged-in" />
                    :
                    <>
                      {
                        !loggedIn ?
                          <React.Fragment>
                            <Link href="https://oauth.botgate.ai/sign-up">
                              <a>
                                <Button type="primary">
                                  Start free trial
                        <img className="ml-8" src="/static/img/icon/right.svg" />
                                </Button>
                              </a>
                            </Link>
                            <Link href="https://oauth.botgate.ai">
                              <a>
                                <Button className="btn-link ml-24 mp-24 p-8 text-thick text-black">
                                  Sign in
                        </Button>
                              </a>
                            </Link>
                          </React.Fragment>
                          :
                          <React.Fragment>
                            <div className="header-logged-in">

                              {
                                subscription && subscription.plan && subscription.plan.isTrial &&
                                <>
                                  <span className="days">{this.getDaysLeft(subscription.endDate)}</span>
                                  <div>
                                    <span>days left </span>
                                    <span>in trial period</span>
                                  </div>
                                </>
                              }
                              <div className="username text-black text-300">
                                {userDetails.name || ''} {userDetails.lastname || ''}
                              </div>
                              <div className="icon-drop">
                                <Popover placement="bottom" overlayClassName="header-popover" content={
                                  <div>
                                    <div>
                                      <Link href="https://app.botgate.ai"><a>Dashboard </a></Link>
                                    </div>
                                    <div>
                                      <Link href="/"><a onClick={logout}> Logout </a></Link>
                                    </div>
                                  </div>
                                } trigger={['click']}>
                                  <img style={{ padding: 8 }} src="/static/img/icon/dots.svg" />
                                </Popover>
                              </div>
                            </div>
                          </React.Fragment>
                      }
                    </>
                }
              </div>
            </div>
          </div>


          <div className="mobile-header p-0">
            <Link href="/">
              <a>
                <img src="/static/img/logo/logo-dark.svg" />
              </a>
            </Link>
            <div className="tablet-header">
              {
                loading ? //TABLET
                <Spin className="tablet-spin"/>
                  :
                  !loggedIn ?
                    <>
                      <div className="tablet-show mobile-hide">
                        <Link href="https://oauth.botgate.ai/sign-up">
                          <a>
                            <Button type="primary">
                              Start free trial
                                  <img className="ml-8" src="/static/img/icon/right.svg" />
                            </Button>
                          </a>
                        </Link>
                      </div>
                      <div className="mt-16 tablet-show mobile-hide">
                        <Link href="https://oauth.botgate.ai">
                          <a>
                            <Button className="btn-link bg-none text-thick text-black">
                              Sign in
                   </Button>
                          </a>
                        </Link>
                      </div>
                    </>
                    :
                    <div className="tablet-show mobile-hide">
                      <div className="header-logged-in">
                        {
                          subscription && subscription.plan && subscription.plan.isTrial &&
                          <>
                            <span className="days">{this.getDaysLeft(subscription.endDate)}</span>
                            <div>
                              <span>days left </span>
                              <span>in trial period</span>
                            </div>
                          </>
                        }
                        <div className="username text-black text-300">
                          {userDetails.name || ''} {userDetails.lastname || ''}
                        </div>
                        <div className="icon-drop">
                          <Popover placement="bottom" overlayClassName="header-popover" content={
                            <div>
                              <div>
                                <Link href="https://app.botgate.ai"><a>Dashboard </a></Link>
                              </div>
                              <div>
                                <Link href="/"><a onClick={logout}> Logout </a></Link>
                              </div>
                            </div>
                          } trigger={['click', 'hover']}>
                            <img src="/static/img/icon/dots.svg" />
                          </Popover>
                        </div>
                      </div>
                    </div>
              }
              <img onClick={() => this.setState({ mobileMenu: true })} className="svg-wrapper d-flex" src="/static/img/icon/menu.svg" />
            </div>
            <Drawer
              title={<div className="mobile-header">
                <Link href="/">
                  <a onClick={() => this.setState({ mobileMenu: false })}>
                    <img src="/static/img/logo/logo-dark.svg" />
                  </a>
                </Link>
                <img onClick={() => this.setState({ mobileMenu: false })} className="svg-wrapper" src="/static/img/icon/close.svg" />
              </div>
              }
              placement="right"
              closable={false}
              onClose={() => this.setState({ mobileMenu: false })}
              visible={this.state.mobileMenu}
            >
              <div className="content text-right">
                <Fade right cascade>
                  <div>
                    {
                      loading ? //MOBILE
                        <span></span>
                        :
                        !loggedIn ?
                          <React.Fragment>
                            <div className="tablet-hide mobile-show">
                              <Link href="https://oauth.botgate.ai/sign-up">
                                <a>
                                  <Button type="primary">
                                    Start free trial
                                  <img className="ml-8" src="/static/img/icon/right.svg" />
                                  </Button>
                                </a>
                              </Link>
                            </div>
                            <div className="mt-16 tablet-hide mobile-show">
                              <Link href="https://oauth.botgate.ai">
                                <a>
                                  <Button className="btn-link bg-none text-thick text-black">
                                    Sign in
                                </Button>
                                </a>
                              </Link>
                            </div>
                          </React.Fragment>
                          :
                          <React.Fragment>
                            <div className=" tablet-hide mobile-show">
                              <div className="header-logged-in">
                                {
                                  subscription && subscription.plan && subscription.plan.isTrial &&
                                  <>
                                    <span className="days">{this.getDaysLeft(subscription.endDate)}</span>
                                    <div>
                                      <span>days left </span>
                                      <span>in trial period</span>
                                    </div>
                                  </>
                                }
                                <div className="username text-black text-300">
                                  {userDetails.name || ''} {userDetails.lastname || ''}
                                </div>
                                <div className="icon-drop">
                                  <Popover placement="bottom" overlayClassName="header-popover" content={
                                    <div>
                                      <div>
                                        <Link href="https://app.botgate.ai"><a>Dashboard </a></Link>
                                      </div>
                                      <div>
                                        <Link href="/"><a onClick={logout}> Logout </a></Link>
                                      </div>
                                    </div>
                                  } trigger={['click', 'hover']}>
                                    <img src="/static/img/icon/dots.svg" />
                                  </Popover>
                                </div>
                              </div>
                            </div>
                          </React.Fragment>
                    }
                    <Divider className="tablet-hide mobile-show" />
                    <h3 onClick={() => this.setState({ mobileMenu: false })}>
                      <Link href="/features">
                        <a>
                          Features
                   </a>
                      </Link>
                    </h3>
                    <h3 onClick={() => this.setState({ mobileMenu: false })}>
                      <Link href="/integrations">
                        <a>
                          Integrations
                  </a>
                      </Link>
                    </h3>

                    <Collapse className="header-collapse" onChange={(e) => this.setState({ addClass: e.length > 0 ? true : false })}>
                      <Collapse.Panel showArrow={false} header={<h3 className="text-bold font-bilgetech">Solutions</h3>} key="1">
                        <Fade right cascade>
                          <div>
                            <div>
                              <Link href="/solutions/e-commerce">
                                <a onClick={() => this.setState({ mobileMenu: false })}>
                                  E-Commerce
                            </a>
                              </Link>
                            </div>
                            <div>
                              <Link href="/solutions/lead-generation">
                                <a onClick={() => this.setState({ mobileMenu: false })}>
                                  Lead Generation
                            </a>
                              </Link>
                            </div>
                            <div>
                              <Link href="/solutions/customer-support">
                                <a onClick={() => this.setState({ mobileMenu: false })}>
                                  Customer Support
                            </a>
                              </Link>
                            </div>
                            <div>
                              <Link href="/solutions/hr-process">
                                <a onClick={() => this.setState({ mobileMenu: false })}>
                                  HR Process
                            </a>
                              </Link>
                            </div>
                          </div>
                        </Fade>
                      </Collapse.Panel>
                    </Collapse>


                    <h3 onClick={() => this.setState({ mobileMenu: false })} className={addClass ? 'mt-0' : ''}>
                      <Link href="/pricing">
                        <a>
                          Pricing
                        </a>
                      </Link>
                    </h3>
                    {/* <h3 onClick={() => this.setState({ mobileMenu: false })}> <Link href="/blog"> <a> Blog </a> </Link> </h3> */}
                  </div>
                </Fade>
              </div>
            </Drawer>
          </div>


        </Layout.Header>
      </React.Fragment>
    );
  };
};

const mapStateToProps = ({ userDetails, subscription }) => ({ userDetails, subscription });

export default withRouter(connect(mapStateToProps)(Header));